@import url("https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap");

/* Arrow.css */
.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}

.arrow {
  font-size: 2rem;
  cursor: pointer;
  color: #006dba;
}

.home-section,
.img1 {
  overflow: hidden;
  position: relative;
  z-index: -1;
  display: flex;
  align-items: center;
  height: 700px;
  min-height: 89vh;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#370b6f),
    color-stop(#00429b),
    color-stop(#006dba),
    color-stop(#0095ce),
    to(#2dbcdc)
  );
  background: linear-gradient(
    to bottom,
    #370b6f,
    #00429b,
    #006dba,
    #0095ce,
    #2dbcdc,
    #273e7e
  );
}

.home-section {
  margin-top: -10px;
}

.head1 {
  position: absolute;
  color: white;
  top: 230px;
  right: 110px;
  font-size: x-large;
  font-family: "Noto Serif", serif;
}

.head2 {
  position: absolute;
  font-size: larger;
  font-weight: 600;
  right: 100px;
  top: 300px;
  flex: 2;
  width: 35%;
}

@media (max-width: 768px) {
  .home-section {
    height: auto;
    min-height: auto;
    padding: 50px 0;
  }

  .head1 {
    top: 100px;
    right: 10px;
    font-size: large;
  }

  .head2 {
    top: 180px;
    right: 10px;
    font-size: medium;
    width: 70%;
  }
}

/* End of head section */

.home-section2 {
  text-align: center;
}
.home-section2 h1 {
  text-align: center;
  margin-top: 50px;
}

/* End of Home section 2 */

.home-section3 {
  margin-top: 30px;
  text-align: center;
}

.home-section3 h1 {
  font-family: "Kanit", sans-serif;
}

.row1 {
  padding: 20px;
}

.grid-container1 {
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  gap: 10px;
  background: linear-gradient(
    to right,
    #28a1bd,
    #36519b
  ); /* Replace these colors with your preferred gradient */

  color: white;
}

.card1 {
  background: linear-gradient(to right, #28a1bd, #36519b);

  color: white;
  height: 550px;
  width: 400px;
  transform: scale(0.9);
  transition: transform 0.3s ease;
  margin-left: 20px;
  margin-bottom: 20px;
}

.card1:hover {
  transform: scale(1);
}

.card1 p {
  font-size: x-large;
}

.btn-home {
  padding: 10px 20px;
  background-color: #00ffd5;
  color: black;
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.btn-home:hover {
  background-color: #9e00b3;
}

/* End of Home Section 3 */

.home-section4 {
  margin-top: 60px;
  display: grid;
  grid-template-columns: auto auto;
  gap: 20px;
}

.section4-one p {
  width: 90%;
}

.section4-para1 {
  color: #ff9800;
  font-weight: bold;
  font-size: large;
}

.section4-two {
  display: flex;
  flex-wrap: wrap;
}

.section4-two li {
  /* position: relative; */
  /* z-index: 1; */
  color: #404040;
  font-size: 16px;
  font-weight: 500;
  display: block;
  padding: 14px 15px;
  border-radius: 5px;
  background-color: #ffffff;
  border-left: 2px solid #7b68ee;
  /* -webkit-box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1); */
  box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1);
  margin-bottom: 10px;
}

.section4-two li:hover {
  width: auto;
  background-color: #7b68ee;
  transition: 0.5s;
  color: white;
  cursor: pointer;
}

.section4-three {
  gap: 20px;
  padding: 20px;
}

/* End of Home section 4 */

.home-section5 {
  padding: 15px;
  margin-top: 40px;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#3b1c57),
    color-stop(#624ea9),
    color-stop(#5e8fd2),
    color-stop(#87c0cd),
    to(#bee0cc)
  );
  background: linear-gradient(
    to bottom,
    /* #3b1c57, */ #624ea9,
    #5e8fd2,
    #87c0cd,
    #bee0cc,
    #d5c7c7,
    #e3a1a1
  );
}

.section5-icon a {
  color: black;
  /* margin-left: 20px; */
  padding: 10px;
}
.section5-icon a:hover {
  color: blue;
}

/* media */

@media screen and (max-width: 768px) {
  .home-section {
    height: 600px;
  }
  .card1 {
    height: 500px;
    width: 280px;
    margin-left: 30px;
  }
  .card1 p {
    font-size: large;
  }
  .head1 {
    font-size: 18px;
    margin-top: 50px;
  }

  .head2 {
    margin-top: 330px;
    width: 90%;
    line-height: 25px;
    font-weight: 600;
    margin-left: 50px;
  }

  .home-section4 {
    padding-left: 10px;
  }
}
