/* End of section - 5 */

.ser6-sec {
  width: 80%;
  overflow: hidden;
  margin-left: 130px;
  background-color: khaki;
  border-radius: 40px;
}

.ser6-sec h1 {
  text-align: center;
}

.image-container {
  display: inline-flex;
  animation: scroll 30s linear infinite;
  height: 170px;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.image-container img {
  width: 100px;
  height: 100px;
  margin-right: 8px;
  padding: 0 20px;
  margin-top: 30px;
}

@media screen and (max-width: 992px) {
  .ser6-sec {
    width: 80%;
