@import url("https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap");

.about-section1 {
  top: 0;
  margin-top: -10px;
}

.about2-sec2 {
  /* margin-top: 40px; */
  background-image: url("../Images/background1.png");
  background-size: 800px;
  background-repeat: no-repeat;
  background: linear-gradient(
    to bottom,
    #370b6f,
    #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
}

.sec2-para1 {
  font-family: "Kanit", sans-serif;
  font-family: "Montserrat", sans-serif;
  font-family: "Noto Serif", serif;
  font-family: "Roboto", sans-serif;
  font-family: "Roboto Slab", serif;
  text-align: justify;
  margin-right: 20px;
}

.about-sec3 {
  text-align: center;
  background-color: blueviolet;

  background: linear-gradient(
    to top,
    #370b6f,
    #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
}

.about-sec3 p {
  font-family: "Kanit", sans-serif;
  font-family: "Montserrat", sans-serif;
  font-family: "Noto Serif", serif;
  font-family: "Roboto", sans-serif;
  font-family: "Roboto Slab", serif;
  margin-left: 15px;
  text-align: justify;
}

.about-sec3 img {
  height: 250px;
}

/* About section -4 */

.sec4-card {
  background: linear-gradient(
    to bottom,
    #370b6f,
    #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
}

.sec4-card img {
  height: 240px;
  width: 190px;
}

.sec4-card1 {
  margin-bottom: 20px;
  margin-right: 20px;
  margin-left: 20px;
  height: 320px;

  background: linear-gradient(
    to bottom,
    #370b6f,
    #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
}

.sec4-card-3 {
  background: linear-gradient(
    to top,
    /* #370b6f, */ #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
}

.sec4-card-3 img {
  height: 240px;
  width: 190px;
}

.sec4-card2 {
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
  background: linear-gradient(
    to top,
    /* #370b6f, */ #00429b,
    #006dba,
    #0095ce,
    #2dbcdc /* #273e7e */
  );
  height: 320px;
}

/* About section-5 */

.about-sec5 img {
  position: relative;
  overflow: hidden;
  z-index: -5;
  height: 500px;
  width: 100%;
  filter: blur(5px);
}

.sec5-one {
  margin-top: -450px;
  z-index: 5;
  text-align: center;
  color: white;
  text-decoration: underline gold;
}

.sec5-two p {
  text-align: center;
  margin-left: 150px;
  color: #ffdb10;

  width: 300px;
  font-size: 16px;
  font-weight: 500;
  display: block;
  padding: 14px 15px;
  border-radius: 5px;
  background-color: rgb(54, 43, 43);
  border-left: 3px solid #006dba;
  /* -webkit-box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1); */
  box-shadow: -1px 3px 20px 0px rgb(82, 90, 101, 0.1);
  margin-bottom: 30px;
}

.sec5-two p:hover {
  width: 250px;
  background-color: #006dba;
  transition: 0.5s;
  color: gold;
  cursor: pointer;
}

@media screen and (max-width: 576px) {
  .about-sec5 img {
    height: 800px;
  }
}

@media screen and (max-width: 576px) {
  .sec5-one {
    margin-top: -800px;
  }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
  .about-sec5 img {
    height: 800px;
    z-index: -10;
  }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
  .sec5-one {
    margin-top: -800px;
  }
}

.form {
  margin-top: 100px;
}

/* end of About section 6 */

.about-sec6 {
  position: relative;
  margin-top: 25px;
  height: 350px;
  background: linear-gradient(
    to bottom,
    /* #6a0dad,
    #8a2387,
    #e94057, */ #f27121,
    #f8b400,
    #facb00,
    #f7d800,
    #eff500,
    #c4edde /* #71e3b8, */ /* #00c2ba, */ /* #00a2a9  */
  );
}

.sec6-space {
  text-align: justify;
  padding: 10px;
  margin: 20px;
  background-color: transparent;
  height: 200px;
  box-shadow: -1px 7px 20px 0px rgb(82, 90, 101, 0.1);
}

.sec6-para1 {
  margin-left: 280px;
}

@media screen and (max-width: 992px) {
  .about-sec6 {
    height: 850px;
  }
}

@media screen and (min-width: 992px) and (max-width: 1200px) {
  .about-sec6 {
    height: 850px;
  }
}

.about-foot {
  margin-top: -40px;
}

/* media */

@media screen and (max-width: 768px) {
  .about2-sec2 {
    padding: 0 10px;
  }
  .about-sec3 {
    padding: 0 10px;
  }
  .about-sec3 img {
    width: 300px;
  }
  .sec4-card img {
    width: 250px;
  }
  .sec4-card {
    padding-top: 50px;
  }

  .sec4-card1 {
    height: 550px;
    width: 300px;
  }

  .sec4-card2 {
    height: 550px;
    width: 300px;
    padding-bottom: 50px;
  }

  .sec4-card2 img {
    width: 250px;
  }
  .sec5-two p {
    margin-left: 50px;
    width: 240px;
  }

  .about-sec6 {
    height: 1050px;
    padding-top: 30px;
  }

  .sec6-para1 {
    margin-left: auto;
  }
  .sec6-space {
    height: 250px;
  }
}
