@import url("https://fonts.googleapis.com/css2?family=Afacad&family=Kanit&family=Montserrat:wght@600&family=Noto+Serif&family=Roboto&family=Roboto+Slab:wght@500&display=swap");

.contact-sec1 img {
  height: 650px;
  width: 100%;
  margin-top: -10px;
  position: relative;
}

.contact-sec1 {
  display: flex;
  align-items: center;
}

.con-sec1 {
  position: absolute;
  flex: 1;
  padding: 20px;
  width: 55%;
  font-size: large;
  font-weight: 500;
}

.con-sec1 h1 {
  color: #333;
}

/* End of contact Section 1 */

.con-sec2 h1 {
  margin-top: 50px;
  text-align: center;
}

.con-sec2 p {
  text-align: center;
  width: 80%;
  margin-left: 130px;
  line-height: 30px;
  font-size: large;
  font-family: "Roboto", sans-serif;
}

.con-icon {
  margin-top: 70px;
  text-align: center;
}
.con-icon a {
  padding: 27px;
}

.con-icon a:hover {
  color: red;
}

/* End of section -2 */

.con-sec3 {
  text-align: center;
  height: 300px;
  width: 380px;
  margin-left: 50px;
  margin-bottom: 20px;
  background-color: mistyrose;
  box-shadow: -1px 6px 25px 2px rgb(82, 90, 101, 0.1);
  border-radius: 30px;
}

.con-sec3 p {
  font-size: larger;
  padding: 20px;
  margin-top: 100px;

  font-family: "Montserrat", sans-serif;
}

.con-sec3 span {
  color: rgb(0, 119, 255);
  margin-right: 10px;
  font-size: xx-large;
}

/* End of section 3 */

.con-sec4 {
  margin-top: 70px;
  text-align: center;
}
.con-sec4 p {
  font-size: large;
  font-family: "Roboto", sans-serif;
}

@media screen and (max-width: 992px) {
  .con-sec2 p {
    margin-left: 60px;
  }
}

/* End of contact section - 4 */

.form-1 {
  margin-left: 480px;
  width: 30%;
}

/* Media */

@media screen and (max-width: 768px) {
  .con-icon a {
    padding: 5px;
  }

  .con-sec2 {
    margin-left: -50px;
    text-align: justify;
  }

  .con-sec3 {
    height: 400px;
    width: 280px;
    margin-left: 50px;
  }

  .con-sec3-para {
    /* display: none;  */
    font-size: xx-small;
    margin-left: -10px;
  }

  .con-sec4 {
    font-size: x-small;
    margin-left: -50px;
  }

  .form-1 {
    margin-left: 22px;
    width: 80%;
    text-align: justify;
    font-size: small;
  }
}
