@import url("https://fonts.googleapis.com/css2?family=Afacad&family=Kanit&family=Montserrat:wght@600&family=Noto+Serif&family=Roboto&family=Roboto+Slab:wght@500&display=swap");

.career-sec1 img {
  height: 630px;
  width: 100%;
  margin-top: -10px;
  position: relative;
}

.career-sec1 {
  display: flex;
  align-items: center;
}

.car-sec {
  flex: 1;
  position: absolute;
  left: 40px;
  color: rgb(232, 255, 23);

  font-family: "Kanit", sans-serif;
}

.car-sec2 {
  background: linear-gradient(
    to bottom,
    /* #624ea9, */ #5e8fd2,
    #87c0cd,
    #bee0cc,
    #f0ffff,
    #d5c7c7,
    #e3a1a1
  );
}

/* End of Section 1 */

.sec2-head {
  left: 40px;
}

.sec2-head p {
  width: 90%;
  text-align: justify;
  font-size: large;
  font-family: "Hedvig Letters Sans", sans-serif;
  font-family: "Kanit", sans-serif;
}

.sec2-img2 img {
  height: 350px;
  width: 300px;
}

/* End of section 2 */

.car-sec3 {
  position: relative;
  display: flex;
  align-items: center;
}
.car-sec3 img {
  margin-top: 30px;
}

.sec3-form {
  position: absolute;
}

.sec3-form h2 {
  font-family: "Montserrat", sans-serif;
}

.foot-one {
  margin-top: -50px;
}

/* Media */

@media screen and (max-width: 7687px) {
  .sec2-img2 img {
    margin-left: 30px;
  }

  .sec3-form h2 {
    text-align: center;
  }
  .sec3-form {
    margin-top: 10px;
    padding: 0 20px;
  }
}
